











































import Vue from 'vue'
import * as L from 'leaflet';
import 'leaflet.markercluster';
import 'leaflet.heat/dist/leaflet-heat.js'
import Papa from 'papaparse'
import TimeLine from '@/components/TimeLine.vue';
import axios from 'axios';
import Helper from '@/helpers/Helper';
import { chartDataOptionsType, chartDataType, selectedGeoData, selectedLayerType } from '@/types/types';
import { Marker } from 'leaflet';
import { appConfig } from '@/config/config';

let appMap: any = {};
let markersLayer: any;
let heatMapLayer: any;

type data = {
    [ k: string ]: any
    chartData: chartDataType
    selectedDate: string
    selectedLayer: selectedLayerType
    allowedLayers: selectedLayerType[]
    slider: number
};

export default Vue.extend( {
    name: 'GeoMap',

    components: {
        TimeLine
    },

    data: (): data => ( {
        slider: 0,
        sliderLabels: [],
        chartData: {
            labels: [],
            datasets: []
        },
        layer: {
            confirmed: {},
            death: {},
            recovered: {}
        },
        selectedDate: '',
        selectedLayer: {
            id: '',
            label: '',
            totalCount: 0
        },
        allowedLayers: [
            { id: 'confirmed', label: 'Confirmed', totalCount: 0 },
            { id: 'death', label: 'Death', totalCount: 0 },
            { id: 'recovered', label: 'Recovered', totalCount: 0 }
        ],
        activeCasesCount: 0,
        drawClusterLayer: true,
        drawHeatMapLayer: false,
        nowPlaying: false
    } ),
    computed: {
        layerLabel: function () {
            for ( let k of this.allowedLayers ) {
                if ( this.selectedLayer.id === k.id ) {
                    return k.label;
                }
            }
            return '';
        },
    },

    watch: {
        selectedDate: function ( val ) {
            this.slider = this.sliderLabels.indexOf( val );
        }
    },

    async mounted() {
        this.initMap();
        this.confirmed = await this.fetchData( appConfig.chart.data.confirmedFileName, appConfig.chart.options.confirmed );
        this.death = await this.fetchData( appConfig.chart.data.deathsFileName, appConfig.chart.options.death );
        this.recovered = await this.fetchData( appConfig.chart.data.recoveredFileName, appConfig.chart.options.recovered );

        this.selectedLayer.id = appConfig.defaultSelectedLayer.id;
        this.selectedLayer.label = this.layerLabel;
        this.selectedLayer.totalCount = this[ this.selectedLayer.id ].totalCount;
        this.selectedDate = this[ this.selectedLayer.id ].mapIndexDate[ this[ this.selectedLayer.id ].lastDate ];

        this.computeSlider();

        this.drawClustersForSelectedLayer();

        this.drawChart();

        this.emitSelectedData();
    },

    methods: {

        selectDateInChart() {
            //TODO activate chart bar item
        },

        sliderValueChanged( v ) {
            this.selectedBarItem( this.sliderLabels[ v ] );
        },

        computeSlider() {
            let mont = this.getParsedSelectedDate().month;
            this.sliderLabels = this[ this.selectedLayer.id ].monthsChart[ mont ].chartData.labels;
            this.slider = this[ this.selectedLayer.id ].monthsChart[ mont ].chartData.labels.length;
        },

        // all incidents from begin
        // pushTotalCountToAllowedLayers() {
        //     for ( let k of this.allowedLayers ) {
        //         k.totalCount = this[ k.id ].totalCount;
        //     }
        //     this.activeCasesCount = this.confirmed.totalCount - this.death.totalCount - this.recovered.totalCount;
        // },

        getAllowedLayerById( id: string ) {
            for ( let k of this.allowedLayers ) {
                if ( k.id === id ) {
                    return k;
                }
            }
            return false;
        },

        pushTotalCountToAllowedLayersForSelectedMonth() {
            let month = this.getParsedSelectedDate().month;
            for ( let k of this.allowedLayers ) {
                k.totalCount = this[ k.id ].monthsChart[ month ].chartData.datasets[ 0 ].data[ this[ k.id ].monthsChart[ month ].chartData.datasets[ 0 ].data.length - 1 ];
            }
            let confirmed = this.getAllowedLayerById( 'confirmed' );
            let death = this.getAllowedLayerById( 'death' );
            let recovered = this.getAllowedLayerById( 'recovered' );
            this.activeCasesCount = confirmed.totalCount - death.totalCount - recovered.totalCount;
        },

        parseDate( date: string ) {
            let parsed = [];
            if ( date ) {
                parsed = date.split( '/' );
                return {
                    month: parsed[ 0 ],
                    day: parsed[ 1 ],
                    year: parsed[ 2 ]
                }
            }
            return {
                month: '',
                day: '',
                year: ''
            }
        },

        emitSelectedData() {

            this.pushTotalCountToAllowedLayersForSelectedMonth();

            this.computeSlider();

            let data: selectedGeoData = {
                selectedLayer: this.selectedLayer,
                selectedDate: this.selectedDate,
                allowedLayers: this.allowedLayers,
                dateData: this[ this.selectedLayer.id ].dateData,
                selectedMonth: this.getParsedSelectedDate().month,
                activeCasesCount: this.activeCasesCount
            };
            this.$emit( 'selectedLayerChanged', data );
        },

        initMap() {
            let bounds = new L.LatLngBounds( new L.LatLng( 90, 180 ), new L.LatLng( -90, -180 ) );
            appMap = L.map( 'mapid', {
                maxZoom: appConfig.maxZoom,
                minZoom: appConfig.minZoom,
                maxBoundsViscosity: 1.0,
                maxBounds: bounds,
                center: bounds.getCenter(),
            } );
            L.tileLayer( 'https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', {
                attribution: 'Map data &copy; <a target="_blank" href="https://www.openstreetmap.org/">OpenStreetMap</a> contributors'
            } ).addTo( appMap );
        },

        async fetchData( url: string, chartOptions: chartDataOptionsType ) {
            let result = await axios.get( appConfig.appUrl + url );
            if ( result.status === 200 ) {
                let parsedData = Papa.parse( result.data );
                return Helper.csvToGeoJson( parsedData, chartOptions );
            }
        },

        getParsedSelectedDate(): { month: string, day: string, year: string } {
            let parsed = this.selectedDate.split( '/' );
            return {
                month: parsed[ 0 ],
                day: parsed[ 1 ],
                year: parsed[ 2 ]
            }
        },

        getGeoJsonForSelectedDate() {
            let parsedDate = this.getParsedSelectedDate();
            return this[ this.selectedLayer.id ].timeline[ parsedDate.month ][ this.selectedDate ].featureCollection;
        },

        drawClustersForSelectedLayer() {
            this.drawClusters( this.getGeoJsonForSelectedDate() );
            this.drawHeatMap();
        },

        drawChart() {

            this.chartData.labels = [];
            this.chartData.datasets = [];
            let month = this.getParsedSelectedDate().month;

            this.chartData.labels = this[ this.selectedLayer.id ].monthsChart[ month ].chartData.labels;
            this.chartData.datasets.push( this.confirmed.monthsChart[ month ].chartData.datasets[ 0 ] );
            this.chartData.datasets.push( this.death.monthsChart[ month ].chartData.datasets[ 0 ] );
            this.chartData.datasets.push( this.recovered.monthsChart[ month ].chartData.datasets[ 0 ] );
            Vue.set( this, 'chartData', this.chartData );
            this.$nextTick( () => {
                if ( this.$refs.timeLine ) {
                    this.$refs.timeLine.updateChart();
                }
            } );
        },

        drawHeatMap() {
            let options = {
                radius: 35,
                gradient: appConfig.heatMap[ this.selectedLayer.id ],
                blur: 50
            };
            if ( heatMapLayer ) {
                if ( !this.drawHeatMapLayer ) {
                    appMap.removeLayer( heatMapLayer );
                    heatMapLayer = null;
                    return false;
                }
                let heatMapData = Helper.geoJson2heat( this.getGeoJsonForSelectedDate() );
                heatMapLayer.setOptions( options );
                heatMapLayer.setLatLngs( heatMapData );
            } else {
                if ( this.drawHeatMapLayer ) {
                    let heatMapData = Helper.geoJson2heat( this.getGeoJsonForSelectedDate() );
                    heatMapLayer = L.heatLayer( heatMapData, options ).addTo( appMap );
                }
            }
        },

        drawClusters( geoJsonData: any ) {
            if ( markersLayer ) {
                appMap.removeLayer( markersLayer );
                if ( !this.drawClusterLayer ) {
                    return false;
                }
            }

            let myIcon = L.icon( {
                iconUrl: require( '@/assets/images/marker-icon.png' ),
                iconRetinaUrl: require( '@/assets/images/marker-icon-2x.png' ),
                iconSize: [ 25, 41 ],
                iconAnchor: [ 15, 41 ], //offset
                popupAnchor: [ -3, -35 ]
            } );

            markersLayer = L.markerClusterGroup( {
                removeOutsideVisibleBounds: true,
                spiderfyOnMaxZoom: false,
                animateAddingMarkers: false,
                singleMarkerMode: true,
                iconCreateFunction: function ( cluster ) {
                    let clusterCountWithIncidents = 0;
                    let childMarkers = cluster.getAllChildMarkers();
                    for ( let k in childMarkers ) {
                        clusterCountWithIncidents += childMarkers[ k ].feature.properties.countIncidents;
                    }
                    let c = ' marker-cluster-';
                    if ( clusterCountWithIncidents < 100 ) {
                        c += 'small';
                    } else if ( clusterCountWithIncidents < 1000 ) {
                        c += 'medium';
                    } else {
                        c += 'large';
                    }
                    let len = ( clusterCountWithIncidents + '' ).length;
                    let width = ( len <= 3 ? len * 14 : len * 12 );
                    let styleWidth = width - 10;
                    return new L.DivIcon( {
                        html: '<div style="width:' + ( styleWidth ) + 'px; height: ' + ( styleWidth ) + 'px;"><span>' + clusterCountWithIncidents + '</span></div>',
                        className: 'marker-cluster' + c,
                        iconSize: new L.Point( width, width )
                    } );
                }
            } );

            let geoJsonLayer = L.geoJSON( geoJsonData, {
                onEachFeature: function ( feature, layer: Marker ) {
                    layer.setIcon( myIcon );
                    layer.bindPopup( feature.properties.region );
                }
            } );

            markersLayer.addLayer( geoJsonLayer );
            appMap.addLayer( markersLayer );
            appMap.fitBounds( markersLayer.getBounds() );
        },

        selectedBarItem( date ) {
            this.selectedDate = date;
            this.drawClustersForSelectedLayer();
            this.emitSelectedData();
        },

        switchOnLayer( newSelectedLayer ) {
            this.selectedLayer = newSelectedLayer;
            this.emitSelectedData();
            this.drawClustersForSelectedLayer();
        },

        setSelectedMonth( month ) {
            this.selectedDate = this[ this.selectedLayer.id ].dateData[ month ].lastDate;
            this.emitSelectedData();
            this.drawClustersForSelectedLayer();
            this.drawChart();

        },

        playSelectedMonth() {
            if ( !this.nowPlaying ) {
                appMap.fitBounds( markersLayer.getBounds() );
                setTimeout( () => {
                    this.nowPlaying = true;
                    let index = 0;
                    this.drawClusterLayer = false;
                    let oldState = this.drawHeatMapLayer;
                    this.drawHeatMapLayer = true;
                    let interval = setInterval( () => {
                        if ( this[ this.selectedLayer.id ].chartData.labels[ index ] ) {
                            this.selectedBarItem( this[ this.selectedLayer.id ].chartData.labels[ index ] );
                            this.drawChart();
                        } else {
                            clearInterval( interval );
                            this.nowPlaying = false;
                            this.drawHeatMapLayer = oldState;
                            this.drawClusterLayer = true;
                            this.drawClustersForSelectedLayer();
                        }
                        index++;
                    }, 100 );
                }, 1000 );
            }
        },

        toggleHeatMap() {
            this.drawHeatMapLayer = !this.drawHeatMapLayer;
            this.drawHeatMap();
        }
    }
} )
