import { Bar } from 'vue-chartjs';
import { Component, Vue } from 'vue-property-decorator';
import { ChartData } from 'chart.js';
import { Prop } from 'vue-property-decorator';

@Component( {
    extends: Bar
} )
export default class BarChart extends Vue<Bar> {

    @Prop( {
        type: Object,
        default: function () {
            return {
                labels: [],
                datasets: []
            };
        }
    } )
    public chartData: ChartData;

    updateChart() {
        this.$data._chart.update();
    }

    mounted() {
        let componentVue = this;

        //@ts-ignore
        Chart.pluginService.register( {
            afterInit: function ( chart, options ) {
                chart.pluginInitialized = true;
            },
            afterDraw: function ( chart, options ) {
                if ( chart.pluginInitialized ) {
                    var centerIndex = Math.floor( chart.data.labels.length / 2 );
                    for ( var i = 0; i < chart.data.datasets.length; i++ ) {
                        chart.getDatasetMeta( i ).data.forEach( function ( ele, idx ) {
                            var opacity = ' 0.3)';
                            if ( idx == centerIndex ) {
                                opacity = ' 1)';
                            }
                            ele.custom = ele.custom || {};
                            if ( ele.custom.backgroundColor == undefined ) {
                                var rgb = ele._model.backgroundColor;
                                var match = null;
                                if ( match = rgb.match( /^#([a-fA-F0-9]{6})$/i ) ) {
                                    rgb = 'rgb(';
                                    match = match[ 1 ];
                                    for ( var j = 0; j < 3; j++ ) {
                                        rgb += parseInt( match.slice( j * 2, j * 2 + 2 ), 16 ) + ',';
                                    }
                                    rgb += opacity;
                                }
                                ele.custom.backgroundColor = rgb;
                            }
                        } );
                    }
                    chart.pluginInitialized = false;
                    chart.update( true );
                }
            }
        } );


        this.renderChart( this.chartData, {
            legend: { position: 'bottom' },
            maintainAspectRatio: false,
            onClick: function ( point, event ) {
                const elements = this.getElementsAtXAxis( point );
                if ( elements ) {
                    const item = elements[ 0 ];
                    componentVue.$emit( 'selectedBarItem', item._model.label );
                    /**
                     * Thanks
                     * @link https://stackoverflow.com/a/52262374
                     */
                    for ( let i = 0; i < this.data.datasets.length; i++ ) {
                        this.getDatasetMeta( i ).data.forEach( function ( ele, idx ) {
                            let rgx = /(rgba*\(\d{1,3}), *(\d{1,3}), *(\d{1,3}), *[0-9.]+(\))/;
                            ele._model.backgroundColor = ele._model.backgroundColor.replace( rgx, '$1, $2, $3$4' );
                            if ( ele.custom !== undefined ) {
                                delete ele.custom.backgroundColor;
                            }
                        } );
                    }

                    let active = this.active.length > 0 ? this.active : elements;
                    // set backgrounf opacity....
                    active.forEach( function ( ele ) {
                        ele.custom = ele.custom || {};
                        if ( ele.custom.backgroundColor === undefined ) {
                            ele.custom.backgroundColor = ele._model.backgroundColor.replace( ')', ', 0.99)' );
                        } else {
                            delete ele.custom.backgroundColor;
                        }
                    } );
                    this.update( true );

                } else {
                    console.error( 'Can not get elements in "Bar"' );
                }
            },
            responsive: true,
            tooltips: {
                mode: 'index',
                intersect: false,
                callbacks: {
                    labelColor: function ( tooltipItem, chart ) {
                        return {
                            borderColor: chart.data.datasets[ tooltipItem.datasetIndex ].backgroundColor,
                            backgroundColor: chart.data.datasets[ tooltipItem.datasetIndex ].hoverBackgroundColor
                        };
                    },
                }
            },
            scales: {
                xAxes: [ {
                    stacked: true,
                    // barPercentage: 1,
                    gridLines: {
                        display: false
                    },
                    ticks: {
                        callback: function ( value, index, values ) {
                            if ( index % 2 === 1 ) {
                                return '';
                            }
                            return value;
                        }
                    }
                } ],
                yAxes: [ {
                    stacked: true,
                    gridLines: {
                        display: false
                    },
                    ticks: {
                        display: false
                    }
                } ]
            }
        } )
    }
}
