




































































































































import Vue from 'vue';
import GeoMap from './components/GeoMap.vue';
import About from './components/About.vue';
import { selectedGeoData } from '@/types/types';
import { appConfig } from '@/config/config';
import Helper from '@/helpers/Helper';
import Share from './components/Share.vue';
import AppNote from './components/AppNote.vue';

export default Vue.extend( {
    name: 'App',

    components: {
        GeoMap,
        About,
        Share,
        AppNote
    },

    data: (): selectedGeoData & { [ key: string ]: any } => ( {
        selectedLayer: {
            id: '',
            label: '',
            totalCount: 0
        },
        selectedMonth: '',
        selectedDate: '',
        allowedLayers: [],
        sheet: false,
        dateData: {},
        months: appConfig.months,
        activeCasesCount: 0,
        windowWidth: 1000,
        shareDialog: true
    } ),

    computed: {
        showLikeIcon: function () {
            return this.windowWidth < 900;
        }
    },

    mounted() {

        this.windowWidth = window.innerWidth;

        // First we get the viewport height and we multiple it by 1% to get a value for a vh unit
        let vh = window.innerHeight * 0.01;
        // Then we set the value in the --vh custom property to the root of the document
        document.documentElement.style.setProperty( '--vh', `${ vh }px` );

        // We listen to the resize event
        window.addEventListener( 'resize', () => {
            // We execute the same script as before
            let vh = window.innerHeight * 0.01;
            document.documentElement.style.setProperty( '--vh', `${ vh }px` );
            this.windowWidth = window.innerWidth;
        } );
    },

    methods: {
        selectLayer( layer ) {
            this.$refs.geoMap.switchOnLayer( layer );
        },

        selectedLayerChanged( event: selectedGeoData ) {
            this.selectedLayer = event.selectedLayer;
            this.selectedDate = event.selectedDate;
            this.allowedLayers = event.allowedLayers;
            this.dateData = event.dateData;
            this.selectedMonth = event.selectedMonth;
            this.activeCasesCount = event.activeCasesCount;
        },

        selectMonth( month ) {
            this.$refs.geoMap.setSelectedMonth( month );
        },

        playSelectedMonth() {
            this.$refs.geoMap.playSelectedMonth();
        },

        toggleHeatMap() {
            this.$refs.geoMap.toggleHeatMap();
        },

        parseNumber( value ) {
            return Helper.numberWithSpaces( value );
        }
    }
} );
