import { chartDataOptionsType, chartDataType } from '@/types/types';

export default class Helper {
    static csvToGeoJson( parsedCsv, chartDataOptions: chartDataOptionsType = {
        label: 'Label 1',
        color: '#ccc',
        bgColor: '#cc6268'
    } ) {
        if ( parsedCsv.data ) {
            let startDataIndex = 4;
            let times = {};
            let dateData = {};
            let mapIndexDate = {};
            let chartData: chartDataType = {
                labels: [],
                datasets: []
            };

            let monthsChart: { [ key: string ]: { chartData: chartDataType } } = {};

            chartData.datasets.push( {
                label: chartDataOptions.label,
                backgroundColor: chartDataOptions.color,
                hoverBackgroundColor: chartDataOptions.bgColor,
                barThickness: 25,
                data: []
            } );

            for ( let i = startDataIndex; i < parsedCsv.data[ 0 ].length; i++ ) {
                mapIndexDate[ i ] = parsedCsv.data[ 0 ][ i ];//date
                chartData.labels.push( parsedCsv.data[ 0 ][ i ] )
            }

            for ( let k = 1; k < parsedCsv.data.length; k++ ) {
                let record = parsedCsv.data[ k ];
                let lat = record[ 2 ];
                let lon = record[ 3 ];
                let region = record[ 1 ];
                let state = record[ 0 ];
                for ( let j = startDataIndex; j < record.length; j++ ) {
                    let countIncidents = Number( record[ j ] );
                    let parsedDate = mapIndexDate[ j ].split( '/' );
                    if ( !times[ parsedDate[ 0 ] ] ) {
                        times[ parsedDate[ 0 ] ] = {};
                        dateData[ parsedDate[ 0 ] ] = {};
                        monthsChart[ parsedDate[ 0 ] ] = {
                            chartData: {
                                labels: [],
                                datasets: [ {
                                    label: chartDataOptions.label,
                                    backgroundColor: chartDataOptions.color,
                                    hoverBackgroundColor: chartDataOptions.bgColor,
                                    // barPercentage: 1,
                                    // categoryPercentage: 1.0,
                                    categoryPercentage: 1.0,
                                    barPercentage: .99,
                                    // barThickness: 20,
                                    // maxBarThickness: 30,
                                    minBarLength: 3,
                                    data: []
                                } ]
                            }
                        };
                    }

                    if ( !times[ parsedDate[ 0 ] ][ mapIndexDate[ j ] ] ) {
                        times[ parsedDate[ 0 ] ][ mapIndexDate[ j ] ] = {
                            featureCollection: {
                                type: 'FeatureCollection',
                                features: []
                            },
                        };
                        // times[ parsedDate[ 0 ] ].chartData.labels.push( mapIndexDate[ j ] );
                        monthsChart[ parsedDate[ 0 ] ].chartData.labels.push( mapIndexDate[ j ] );
                    }
                    dateData[ parsedDate[ 0 ] ].lastDate = mapIndexDate[ j ];
                    times[ parsedDate[ 0 ] ][ mapIndexDate[ j ] ].featureCollection.features.push( {
                        type: 'Feature',
                        id: j + ':' + mapIndexDate[ j ],
                        properties: {
                            region: region + ( state ? ' (' + state + ')' : '' ),
                            countIncidents: countIncidents,
                        },
                        geometry: {
                            'type': 'Point',
                            'coordinates': [ Number( lon ), Number( lat ) ]
                        }
                    } );
                    let chartDataIndex = j - startDataIndex;
                    // let monthDataArray = monthsChart[ parsedDate[ 0 ] ].chartData.datasets[0].data;
                    if ( chartData.datasets[ 0 ].data[ chartDataIndex ] === undefined ) {
                        chartData.datasets[ 0 ].data.push( countIncidents );
                    } else {
                        chartData.datasets[ 0 ].data[ chartDataIndex ] = chartData.datasets[ 0 ].data[ chartDataIndex ] + countIncidents;
                    }
                }
            }
            let dates = Object.values( mapIndexDate );
            for ( let m in monthsChart ) {
                for ( let l of monthsChart[ m ].chartData.labels ) {
                    let dateIndex = dates.indexOf( l );
                    if ( dateIndex !== -1 ) {
                        monthsChart[ m ].chartData.datasets[ 0 ].data.push(
                            chartData.datasets[ 0 ].data[ dateIndex ]
                        );
                    }
                }
            }

            return {
                monthsChart: monthsChart,
                chartData: chartData,
                timeline: times,
                dateData: dateData,
                lastDate: parsedCsv.data[ 0 ].length - 1,
                mapIndexDate: mapIndexDate
            }
        }
    }

    static geoJson2heat( geojson: any ) {
        return geojson.features.map( function ( feature ) {
            return [ parseFloat( feature.geometry.coordinates[ 1 ] ), parseFloat( feature.geometry.coordinates[ 0 ] ), feature.properties[ 'countIncidents' ] ];
        } );
    }

    /**
     * Thanks
     * @link https://ru.stackoverflow.com/a/874799
     * @param x
     */
    static numberWithSpaces( x ) {
        return x.toString().replace( /\B(?=(\d{3})+(?!\d))/g, ' ' );
    }
}
